import React, { useEffect, useState } from "react";
import { BubbleA, BubbleB } from "../SVG/Droplets";
import "./Hero.styles.scss";
import { AnimatePresence, motion } from "framer-motion";

const Hero = ({
  png,
  background,
  textColors,
  index,
  mainTextVariants,
  secondaryTextVariants,
}) => {
  return (
    <motion.section
      id="hero"
      initial={{ background: background[0] }}
      animate={{
        background: background[index],
        transition: { duration: 1.2, ease: "easeIn" },
      }}
    >
      <motion.div
        style={{
          position: "absolute",
          left: 0,
          top: 0,
          width: "150rem",
          height: "150rem",
        }}
        initial={{ x: "-100%", y: "25%" }}
        animate={{ x: "-50%", y: "35%", transition: { duration: 2 } }}
      >
        <BubbleA blur={1} />
      </motion.div>
      <motion.div
        style={{
          position: "absolute",
          right: 0,
          bottom: 0,
          width: "180rem",
          height: "180rem",
        }}
        initial={{ x: "100%", y: "-25%" }}
        animate={{ x: "30%", y: "-35%", transition: { duration: 2 } }}
      >
        <BubbleA blur={1} />
      </motion.div>
      <motion.div
        style={{
          position: "absolute",
          width: "120rem",
          height: "120rem",
        }}
        initial={{ x: "-300%", y: "450%" }}
        animate={{ x: "-10%", y: "130%", transition: { duration: 3.5 } }}
      >
        <BubbleA blur={10} />
      </motion.div>
      <motion.div
        style={{
          top: 0,
          right: 0,
          position: "absolute",
          width: "60rem",
          height: "60rem",
        }}
        initial={{ x: "100%", y: "-125%" }}
        animate={{ x: "-460%", y: "180%", transition: { duration: 3.5 } }}
      >
        <BubbleA blur={10} />
      </motion.div>
      <div className="container">
        <motion.div
          className="heading_container"
          initial={{ x: "10vw", opacity: 0 }}
          animate={{
            x: 0,
            opacity: 1,
            transition: { duration: 1.3 },
          }}
        >
          <motion.h2
            className="main_heading"
            variants={mainTextVariants}
            initial="initial"
            animate="animate"
          >
            BIDONY NA WOD{" "}
            <motion.span
              className="text_color"
              variants={secondaryTextVariants}
              initial="initial"
              animate="animate"
            >
              HYDRATE HEROES
            </motion.span>
          </motion.h2>
          <motion.p
            className="paragraph"
            variants={mainTextVariants}
            initial="initial"
            animate="animate"
          >
            Zadbaj o codzienne nawodnienie organizmu z bidonem
            <motion.span
              className="text_color"
              variants={secondaryTextVariants}
              initial="initial"
              animate="animate"
            >
              {" "}
              HYDRATE HEROES
            </motion.span>
            . Dziki lekkiej i porcznej konstrukcji moesz zabra go ze sob
            wszdzie, a oznaczenia na butelce bd przypomina ci o uzupenianiu
            pynw.
          </motion.p>
          <div className="btn_container">
            {/* <motion.button
              className="main_btn"
              initial={{
                backgroundColor: textColors[0].main,
                color: textColors[0].sec,
              }}
              animate={{
                backgroundColor: textColors[index].main,
                color: textColors[index].sec,
                transition: { duration: 1.2, ease: "easeIn" },
              }}
            >
              KUP
            </motion.button> */}
            <motion.button
              className="sec_btn"
              initial={{
                backgroundColor: textColors[0].sec,
                color: textColors[0].main,
              }}
              animate={{
                backgroundColor: textColors[index].sec,
                color: textColors[index].main,
                transition: { duration: 1.2, ease: "easeIn" },
              }}
              whileHover={{
                backgroundColor: textColors[index].main,
                color: textColors[index].sec,
                transition: { duration: 0.3, ease: "easeIn" },
              }}
            >
              PRODUKTY
            </motion.button>
          </div>
        </motion.div>
        <motion.div
          className="image_container"
          initial={{ x: "15vw", y: "-80vh", rotate: "-25deg" }}
          animate={{
            x: 0,
            y: 0,
            rotate: 0,
            transition: { duration: 1.3, delay: 1 },
          }}
        >
          <motion.div
            style={{
              position: "absolute",
              top: "-6%",
              right: "20%",
              zIndex: 1,
              width: "30rem",
              height: "30rem",
            }}
            animate={{ scale: [0, 1, 1, 0], y: ["15vh", "15vh", "0vh", "0vh"] }}
            transition={{
              duration: 6,
              ease: "easeInOut",
              times: [0, 0.2, 1, 1],
              repeat: Infinity,
              repeatDelay: 2,
              delay: 1.3,
            }}
          >
            <BubbleB
              top={"-6%"}
              right={"20%"}
              index={1}
              blur={"0"}
              width={"30rem"}
            />
          </motion.div>
          <motion.div
            style={{
              position: "absolute",
              top: "1%",
              right: "20%",
              zIndex: 1,
              width: "55rem",
              height: "55rem",
            }}
            animate={{ scale: [0, 1, 1, 0], y: ["13vh", "13vh", "0vh", "0vh"] }}
            transition={{
              duration: 4.5,
              ease: "easeInOut",
              times: [0, 0.2, 1, 1],
              repeat: Infinity,
              repeatDelay: 2.8,
              delay: 1.9,
            }}
          >
            <BubbleB top={"1%"} right={"20%"} index={1} width={"55rem"} />
          </motion.div>
          <motion.div
            style={{
              position: "absolute",
              top: "-4%",
              left: "20%",
              zIndex: -1,
              width: "40rem",
              height: "40rem",
            }}
            animate={{ scale: [0, 1, 1, 0], y: ["15vh", "15vh", "0vh", "0vh"] }}
            transition={{
              duration: 6,
              ease: "easeInOut",
              times: [0, 0.2, 1, 1],
              repeat: Infinity,
              repeatDelay: 2.5,
              delay: 0.3,
            }}
          >
            <BubbleB top={"-4%"} left={"20%"} index={-1} width={"40rem"} />
          </motion.div>
          <motion.div
            style={{
              position: "absolute",
              top: "-3%",
              left: "-3%",
              zIndex: -1,
              width: "60rem",
              height: "60rem",
            }}
            animate={{ scale: [0, 1, 1, 0], y: ["15vh", "15vh", "5vh", "5vh"] }}
            transition={{
              duration: 7,
              ease: "easeInOut",
              times: [0, 0.2, 1, 1],
              repeat: Infinity,
              repeatDelay: 2,
              delay: 2,
            }}
          >
            <BubbleB top={"10%"} left={"10%"} index={-1} width={"60rem"} />
          </motion.div>
          <motion.div
            style={{
              position: "absolute",
              top: "5%",
              left: "37%",
              zIndex: -1,
              width: "25rem",
              height: "25rem",
            }}
            animate={{ scale: [0, 1, 1, 0], y: ["15vh", "15vh", "0vh", "0vh"] }}
            transition={{
              duration: 6,
              ease: "easeInOut",
              times: [0, 0.2, 1, 1],
              repeat: Infinity,
              repeatDelay: 4,
            }}
          >
            <BubbleB top={"5%"} left={"37%"} index={-1} width={"25rem"} />
          </motion.div>
          <AnimatePresence mode="popLayout">
            <motion.img
              key={index}
              className="hero_img"
              src={png[index]}
              alt="bidon, kolor gradient, dwu litrowa"
              initial={{ opacity: 0 }}
              animate={{
                opacity: 1,
                transition: { duration: 1.2, ease: "easeIn" },
              }}
              exit={{ opacity: 0, transition: { duration: 1.2 } }}
            />
          </AnimatePresence>
        </motion.div>
      </div>
    </motion.section>
  );
};

export default Hero;
