import React, { useEffect, useState } from "react";
import Hero from "./Components/Hero";
import Nav from "./Components/Nav";

function App() {
  const png = [
    "/img/2LV1.png",
    "/img/2LV2.png",
    "/img/2LV3.png",
    "/img/2LV4.png",
  ];

  const background = [
    "linear-gradient(0deg, rgba(165, 249, 248, 1) 0%, rgba(160, 146, 255, 1) 90%)",
    "linear-gradient(0deg, rgba(255,159,77,1) 0%, rgba(85,177,255,1) 90%)",
    "linear-gradient(0deg, rgba(159,131,228,1) 0%, rgba(113,185,196,1) 90%)",
    "linear-gradient(0deg, rgba(140,180,255,1) 0%, rgba(255,191,169,1) 90%)",
  ];

  const textColors = [
    { main: "#6C59EA", sec: "#5FFFFD" },
    { main: "#0276F0", sec: "#F58634" },
    { main: "#68FFC4", sec: "#7C53E8" },
    { main: "#5A8ADB", sec: "#FF7777" },
  ];

  const [index, setIndex] = useState(0);

  useEffect(() => {
    png.forEach((picture) => {
      new Image().src = picture;
    });

    const indexInterval = setInterval(() => {
      setIndex((prev) => (prev + 1) % png.length);
    }, 11250);

    return () => {
      clearInterval(indexInterval);
    };
  }, []);

  const mainTextVariants = {
    initial: { color: textColors[0].main },
    animate: {
      color: textColors[index].main,
      transition: { duration: 1.2, ease: "easeIn" },
    },
  };

  const secondaryTextVariants = {
    initial: { color: textColors[0].sec },
    animate: {
      color: textColors[index].sec,
      transition: { duration: 1.2, ease: "easeIn" },
    },
  };

  return (
    <div className="App">
      <Nav
        textColors={textColors}
        index={index}
        mainTextVariants={mainTextVariants}
        secondaryTextVariants={secondaryTextVariants}
      />
      <Hero
        png={png}
        background={background}
        textColors={textColors}
        index={index}
        mainTextVariants={mainTextVariants}
        secondaryTextVariants={secondaryTextVariants}
      />
    </div>
  );
}

export default App;
